<template v-if="show"><el-sub-menu v-if="hasChildren(record)" :index="record.name"><template #title><i :class="record.meta?.icon"></i><span>{{record.meta?.title}}</span></template><SubMenuItem v-for="c in record.children" :key="c.name" :record="c"></SubMenuItem></el-sub-menu><MenuItem v-else :record="record"></MenuItem></template>