
import { defineComponent } from "vue"
import { mapGetters } from "vuex"
import SubMenuItem from "./SubMenuItem.vue"

export default defineComponent({
  name: "Menu",
  components: {
    SubMenuItem,
  },
  computed: {
    ...mapGetters({
      isCollapseMenu: "app/isCollapseMenu",
      menus: "app/getMenus",
      currentActiveMenu: "app/currentActiveMenu",
    }),
  },
})
