<div id="page-tag"> <div class="left-tag"><el-button class="item-btn" v-if="showScrollBtn" :disabled="showScrollLeftBtn" icon="el-icon-arrow-left" size="mini" @click="handleSetScroll('L')"></el-button><el-scrollbar ref="scrollbar" @scroll="handleScroll" wrap-class="scrollbar-wrap-class" view-class="scrollbar-view-class"><div class="item-tags" ref="tags"><el-tag size="small" type="success" v-for="tag in tabs" :key="tag.name" :closable="isClosable(tag)" :effect="getEffect(tag)" @click="setActiveTab(tag)" @close="handleCloseTab(tag)">{{tag.title}}</el-tag></div></el-scrollbar><el-button class="item-btn" v-if="showScrollBtn" :disabled="showScrollRightBtn" icon="el-icon-arrow-right" size="mini" @click="handleSetScroll('R')"></el-button></div><div class="right-tag"><el-button class="item-btn" icon="el-icon-refresh" size="mini" @click="commitReload"></el-button><span class="item-dropdown"><el-dropdown><span class="el-dropdown-link"><el-button class="item-btn" icon="el-icon-arrow-down" size="mini"></el-button></span><template #dropdown><el-dropdown-menu><el-dropdown-item icon="el-icon-close" @click="handleCloseCurrentTab">关闭当前标签页</el-dropdown-item><el-dropdown-item icon="el-icon-back" @click="commitDelLeftTab">关闭左侧标签页</el-dropdown-item><el-dropdown-item icon="el-icon-right" @click="commitDelRightTab">关闭右侧标签页</el-dropdown-item><el-dropdown-item icon="el-icon-finished" @click="commitDelOtherTab">关闭其他标签页</el-dropdown-item><el-dropdown-item icon="el-icon-minus" @click="handleCloseAllTab">关闭全部标签页</el-dropdown-item></el-dropdown-menu></template></el-dropdown></span></div></div>